.App {
  min-height: 100vh;
}

.header {
    width: 100%;
    color: blue;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
}

.header-main {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-main-left {
    width: 15%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 2rem;
}

.header-main-left-courses {
    font-size: 2rem;
}

.header-main-left-courses p {
    margin: 0;
}

.header-main-left-search {
    height: 90%;
    width: 60%;
    border: 0.1rem black solid;
    border-radius: 0.5rem;
    padding: 0.5rem 0;
}

.header-main-left-search input {
    width: 100%;
    height: 2.25rem;
    outline: none;
    border: none;
    /* border: 0.1rem black solid;
    border-radius: 0.5rem; */
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 2rem;
    padding-left: 1rem;
}

.header-main-left-search input::-webkit-input-placeholder {
    font-size: 1.75rem;
    color: blue;
    font-weight: 500;
}

.header-main-left-search input:-ms-input-placeholder {
    font-size: 1.75rem;
    color: blue;
    font-weight: 500;
}

.header-main-left-search input::placeholder {
    font-size: 1.75rem;
    color: blue;
    font-weight: 500;
}

.header-main-logo {
    width: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-main-logo img {
    width: 100%;
    background-color: white;
}

.header-main-right {
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-main-right-item button {
    border: none;
    outline: none;
    font-size: 2rem;
    background: transparent;
    color: blue;
    cursor: pointer;
}

.header-main-right-item p {
    font-size: 2rem;
    font-weight: 500;
    color: blue;
}

hr {
    width: 99%;
    margin: 0;
}

.header-main-logo-none {
    display: none;
}

.header-main-logo-mobile {
    display: none;
}


.menu-options {
    /* width: 60%; */
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.menu-option-options {
    color: blue;
    text-decoration: none;
    transition: 0.2s linear;
    border-bottom: 0.3rem solid white;
}

.active-menu {
    border-bottom: 0.3rem solid #ed7203;
}

.header-logo {
    display: none;
}

.hamburger-menu {
    display: none;
}

.hamburger-menu a {
    color: black;
}

@media only screen and (max-width: 1024px) {
    header {
        height: 10rem;
    }

    .header-logo {
        width: 20%;
    }

    .header-logo img {
        width: 100%;
    }

    .menu-options {
        width: 70%;
    }
}

@media only screen and (max-width: 768px) {
    .header-main {
        display: none;
    }

    .hamburger-menu {
        display: inline-block;
    }

    .menu-options {
        display: none;
    }

    header {
        width: 80%;
        justify-content: space-between;
        padding: 0 10%;
        flex-direction: row;
    }

    .header-logo {
        display: inline-block;
        width: 30%;
    }

    .mobile-menu-options {
        position: fixed;
        width: 40%;
        height: 40rem;
        top: 5rem;
        right: 0;
        background: white;
        color: black;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-evenly;
        z-index: 10000;
        padding-right: 5%;
        border: 0.2rem solid #ed7203;
        border-top: 0;
        border-radius: 0 0 1rem 1rem;
        box-shadow: 0.3rem 1.5rem 1rem rgba(0, 0, 0, 0.3);
    }

    .menu-option-options {
        font-size: 3rem;
    }

    /* .header-main-logo-mobile {
        display: inline-block;
    } */
    .header-main {
        width: 100%;
        flex-direction: column;
    }

    .header-main-left {
        width: 100%;
        height: 30%;
        flex-direction: column;
        align-items: flex-end;
    }

    .header-main-left-search {
        height: 30%;
    }
    

    .header-main-logo {
        display: none;
    }
    
    .header-main-right {
        height: 40%;
        width: 100%;
        flex-direction: column;
        align-items: flex-end;
    }

    .header-main-right-item button {
        font-size: 2rem;
    }
    
}

@media only screen and (max-width: 468px) {
    .header-logo {
        width: 50%;
    }

    .mobile-menu-options {
        position: fixed;
        width: 60%;
        height: 40rem;
    }

    .menu-option-options {
        font-size: 2rem;
    }

    .mobile-menu-options {
        position: fixed;
        width: 70%;
        height: 40rem;
        top: 9rem;
    }
}
.opening-page {
    width: 100%; 
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'lato', sans-serif;
}

.opening-page-top {
    padding-top: 8rem;
    padding-bottom: 5rem;
    background: url(../../static/media/wave.63e2e7dd.svg), url(../../static/media/wave10.47d2fa23.svg);
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.opening-page-top-image {
    width: 40%;
}

.opening-page-top-image img {
    width: 100%;
    height: 80%;
    border-radius: 20% 30% 0 0;
}

.opening-page-top-text {
    width: 35%;
    text-align: right;
}

.opening-page-top-text h1 {
    font-size: 6rem;
    color: #FDBE34;
    text-align: center;
}

.opening-page-top-text p {
    font-size: 2.6rem;
    color: #10316B;
    text-align: justify;
}

.opening-page-why {
    padding-top: 2rem;
    background: url(../../static/media/wave2.97e62b2d.svg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.opening-page-why h1 {
    font-size: 6rem;
    color: #EE6F57;
}

.image-carousel {
    width: 60%;
    padding: 1rem;
    padding-top: 1rem;
    border: 1rem solid #10316B;
}

.opening-page-students {
    padding-top: 10rem;
    padding-bottom: 5rem;
    background: url(../../static/media/wave3.b373a4b8.svg);
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.opening-page-students-image {
    width: 40%;
}

.opening-page-students-image img {
    width: 100%;
    /* height: 80%; */
    border-radius: 20% 50% 5% 10%;
}

.opening-page-students-text {
    width: 35%;
    text-align: right;
}

.opening-page-students-text h1 {
    font-size: 6rem;
    color: #EE6F57;
    text-align: center;
}

.opening-page-students-text p {
    font-size: 2.6rem;
    color: #10316B;
    text-align: justify;
}

.opening-page-teachers {
    padding-top: 5rem;
    padding-bottom: 5rem;
    background: url(../../static/media/wave4.d22b4075.svg);
    background-position: center;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.opening-page-teachers-image {
    width: 40%;
}

.opening-page-teachers-image img {
    width: 100%;
    border-radius: 20% 50% 5% 10%;
}

.opening-page-teachers-text {
    width: 35%;
    text-align: right;
}

.opening-page-teachers-text h1 {
    font-size: 6rem;
    color: #EE6F57;
    text-align: center;
}

.opening-page-teachers-text p {
    font-size: 2.6rem;
    color: #10316B;
    text-align: justify;
}

.opening-page-testimonial {
    padding-top: 2rem;
    background: url(../../static/media/wave5.59fa2141.svg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.opening-page-testimonial h1 {
    font-size: 6rem;
    margin-bottom: 1.75rem;
    color: white;
}

.testimonial-image-carousel {
    width: 80%;
    padding: 1rem;
    padding-top: 1rem;
    border: 1rem solid #FDBE34;
    /* border-radius: 10rem; */
}

.courses-main {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    font-family: 'Lato', sans-serif;
    padding-top: 2rem;
    background: linear-gradient(to right, #FF416C, #FF4B2B);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.courses-main-heading {
    font-size: 4.8rem;
    color: #fdc448;
}

.courses-main-div {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    font-family: 'Lato', sans-serif;
    padding-top: 2rem;
}

.card {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
}

.card-img {
    width: 100%;
    overflow: hidden;
}

.card-img img {
    width: 100%;
    height: 30rem;
    transition: -webkit-transform .5s ease;
    transition: transform .5s ease;
    transition: transform .5s ease, -webkit-transform .5s ease;
    border-radius: 1rem;
}

.card-img:hover img{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.card-img:hover {
    box-shadow: 0 2.8px 2.2px rgba(16, 49, 107, 0.034),
    0 6.7px 5.3px rgba(16, 49, 107, 0.048),
    0 12.5px 10px rgba(16, 49, 107, 0.06),
    0 22.3px 17.9px rgba(16, 49, 107, 0.072),
    0 41.8px 33.4px rgba(16, 49, 107, 0.086),
    0 100px 80px rgba(16, 49, 107, 0.12);
}

.card-body {
    position: relative;
    top: -5rem;
    width: 90%;
    font-size: 1.8rem;
    background: white;
    text-align: justify;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0.8rem;
}

.card-title {
    font-size: 2.4rem;
    text-align: center;
    cursor: pointer;
}

.card-title a {
    text-decoration: none;
    color: #fdbe34;
}

.card-text {
    width: 90%;
}



@media only screen and (max-width: 768px) {
    .opening-page-top-image {
