    }

    .opening-page-teachers {
        flex-direction: column;
    }
    .opening-page-teachers-image {
        width: 90%;
    }
    
    .opening-page-teachers-text {
        width: 90%;
    }
    
    .opening-page-teachers-text h1 {
        font-size: 3.2rem;
    }
    
    .opening-page-teachers-text p {
        font-size: 2rem;
    }

    .opening-page-testimonial h1 {
        font-size: 3.2rem;
        margin-bottom: 1.75rem;
        color: white;
    }
    
    .testimonial-image-carousel {
        width: 100%;
        padding: 0;
        padding: 1rem 0;
        border-left: none;
        border-right: none;
    }
}
.footer {
  position: relative;
  width: 100%;
  background: #3586ff;
  min-height: 10.0rem;
  /* padding: 2.0rem 5.0rem; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  margin-top: 10rem;
  padding-bottom: 2rem;
  z-index: 50;
}

.social-icon,
.menu {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin: 1.0rem 0; */
  flex-wrap: wrap;
  padding: 0;
}

.social-icon__item,
.menu__item {
  list-style: none;
}

.social-icon__link {
  font-size: 2rem;
  color: #fff;
  margin: 0 1.0rem;
  display: inline-block;
  transition: 0.5s;
}
.social-icon__link:hover {
  -webkit-transform: translateY(-1.0rem);
          transform: translateY(-1.0rem);
}

.menu__link {
  font-size: 2rem;
  color: #fff;
  margin: 0 1.0rem;
  display: inline-block;
  transition: 0.5s;
  text-decoration: none;
  opacity: 0.75;
  font-weight: 300;
}

.menu__link:hover {
  opacity: 1;
}

.footer p {
  color: #fff;
  margin: 1.5rem 0 1.0rem 0;
  font-size: 1.6rem;
  font-weight: 300;
}

.wave {
  position: absolute;
  top: -10.0rem;
  left: 0;
  width: 100%;
  height: 10.0rem;
  background: url("https://i.ibb.co/wQZVxxk/wave.png");
  background-size: 100.0rem 10.0rem;
}

.wave#wave1 {
  z-index: 100;
  opacity: 1;
  bottom: 0;
  -webkit-animation: animateWaves 4s linear infinite;
          animation: animateWaves 4s linear infinite;
}

.wave#wave2 {
  z-index: 99;
  opacity: 0.5;
  bottom: 1.0rem;
  -webkit-animation: animate 4s linear infinite !important;
          animation: animate 4s linear infinite !important;
}

.wave#wave3 {
  z-index: 100;
  opacity: 0.2;
  bottom: 1.5rem;
  -webkit-animation: animateWaves 3s linear infinite;
          animation: animateWaves 3s linear infinite;
}

.wave#wave4 {
  z-index: 99;
  opacity: 0.7;
  bottom: 2.0rem;
  -webkit-animation: animate 3s linear infinite;
          animation: animate 3s linear infinite;
}

@-webkit-keyframes animateWaves {
  0% {
    background-position-x: 100.0rem;
  }
  100% {
    background-positon-x: .0rem;
  }
}

@keyframes animateWaves {
  0% {
    background-position-x: 100.0rem;
  }
  100% {
    background-positon-x: .0rem;
  }
}

@-webkit-keyframes animate {
  0% {
    background-position-x: -100.0rem;
  }
  100% {
    background-positon-x: .0rem;
  }
}

@keyframes animate {
  0% {
    background-position-x: -100.0rem;
  }
  100% {
    background-positon-x: .0rem;
  }
}

